.addOwners,
.percentageValue {
	border: 1px solid var(--border-color-2);
	border-radius: 4px;
	margin-bottom: 28px;
	padding: 12px 16px;
	align-items: center;
	justify-content: space-between;
	font-size: 14px;
	margin-top: 8px;
	margin-bottom: 24px;
}

.addOwners {
	padding: 22px 16px;
	cursor: pointer;
}

.owners {
	border: 1px solid var(--border-color-2);
	border-radius: 4px;
	padding: 0 18px 0 16px;
	margin-top: 8px;
}

.owner {
	display: flex;
	justify-content: space-between;
	line-height: 64px;
	font-size: 14px;
	border-bottom: 1px solid var(--border-color-1);
}

.owner:last-of-type {
	border: none;
}
